.form__button {
  width: 100%;
  margin: 30px auto;
  padding: 15px;

  color: white;
  font-size: 20px;
  font-family: inherit;

  background-color: rgb(100, 100, 100);
  border: 0;
  cursor: pointer;

  transition: opacity .4s;
}

.form__button:hover {
  opacity: .7;
  scale: 1.05;

  transition: scale .3s ease;
}
